.p {
	z-index: -1;
	position: fixed;
	left: 0px;
	top: 50px;
	width: 1px;
	height: 1px;
	background-color: white;
	position: fixed;
	animation-name: particleAnimation;
	animation-timing-function: infinite;
	animation-iteration-count: infinite;
	-ms-transform: rotate(20deg); /* IE 9 */
	transform: rotate(20deg);
}
.p::before {
	position: absolute;
	display: block;
	content: "";
	width: 100px;
	right: 1px;
	top: 0px;
	height: 1px;
	background: linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgba(255, 255, 255, 0.4) 100%);
}
.p-1 {
	animation-duration: 20s;
	left: 10%;
}
.p-2 {
	animation-duration: 10s;
	top: 60%;
	left: 40%;
}
.p-3 {
	animation-duration: 40s;
	top: 90%;
	left: 60%;
}

.p-4 {
	animation-duration: 15s;
	left: 25%;
}
.p-5 {
	animation-duration: 11s;
	top: 30%;
	left: 20%;
}
.p-6 {
	animation-duration: 50s;
	top: 33%;
	left: 10%;
}

.p-7 {
	animation-duration: 5s;
	left: 50%;
}
.p-8 {
	animation-duration: 3s;
	top: 10%;
	left: 30%;
}
.p-9 {
	animation-duration: 11s;
	top: 40%;
	left: 60%;
}

.p-11 {
	animation-duration: 7s;
	left: 10%;
}
.p-21 {
	animation-duration: 5s;
	top: 60%;
	left: 40%;
}
.p-31 {
	animation-duration: 11s;
	top: 90%;
	left: 60%;
}

.p-41 {
	animation-duration: 4s;
	left: 25%;
}
.p-51 {
	animation-duration: 2s;
	top: 30%;
	left: 20%;
}
.p-61 {
	animation-duration: 1s;
	top: 33%;
	left: 10%;
}

.p-71 {
	animation-duration: 4s;
	left: 50%;
}
.p-81 {
	animation-duration: 1s;
	top: 10%;
	left: 30%;
}
.p-91 {
	animation-duration: 4s;
	top: 40%;
	left: 60%;
}

h1,
h3,
p,
label {
	color: whitesmoke;
}

@keyframes particleAnimation {
	from {
		top: -100px;
	}

	50% {
		transform: translateY(500px);
	}
	to {
		left: calc(100% + 100px);
		top: calc(100% + 100px);
	}
}

@keyframes tothetop {
	from {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		min-height: 100vh;
		background: #0c0c2b;
	}

	to {
		display: block;
		align-items: none;
		justify-content: none;
		min-height: 0vh;
		align-items: center;
	}
}

@keyframes moonanim {
	from {
		top: -50vh;
	}

	to {
		top: 0;
	}
}

.logo {
	color: white;
	font-family: Sail;
}

img {
	-webkit-user-drag: none;
	user-select: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	pointer-events: none;
}

